  position: relative;
}
.footer__nav-link.focus-visible:focus {
  outline: 1px solid #009dff;
  outline-offset: 2px;
}
.footer__nav-link:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: -4px;
  left: 0;
  background-color: #049fff;
  color: #049fff;
  transition: width 0.5s;
}
.footer__nav-link::after {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  bottom: -4px;
  left: 0;
  background-color: #049fff;
  color: #049fff;
  transition: width 0.5s;
}

.social {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 11px;
  row-gap: 11px;
  align-items: center;
  width: 97px;
}

.social__item {
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50px;
  position: relative;
  cursor: pointer;
}
.social__item:hover .social__link-icon {
  fill: #049fff;
}
.social__item:focus-within {
  outline: 1px solid #009dff;
  outline-offset: 2px;
}

.social__link-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #030a1a;
}

